    .menu {
        position: absolute;
        top: 0;
        right: 0;
        background-color: #fff;
        width: 100vw;
        height: 100%;
        padding-top: 120px;
        box-shadow: inset rgb(92 83 83 / 50%) -1px 0px 20px 13px, inset rgb(215 205 205 / 90%) 1px -20px 12px 14px, rgb(0 0 0 / 90%) -1px 2px 17px 0px;
        overflow: hidden;
        z-index: 3;
    }



    .close {
        cursor: pointer;
        width: 30px;
        height: 30px;
        position: absolute;
        top: 7px;
        right: 15px;
    }

    .list {
        list-style: none;
        padding: 0;
    }

    .item:first-child {
        border-top: 1px solid rgb(100, 95, 95);
        padding-top: 15px;
    }

    .item {
        border-bottom: 1px solid rgb(100, 95, 95);
        width: 200px;
        margin: 0 auto;
        padding-bottom: 15px;
        margin-bottom: 15px;
    }


    .activeLink {
        position: relative;
        color: #009cff;
        font-size: 15px;
        line-height: 1.15;
        font-family: 'Montserrat';
    }

    .buttonExit {
        border: none;
        background-color: transparent;
        cursor: pointer;
        color: black;
        font-size: 15px;
        line-height: 1.15;
        font-family: 'Montserrat';
        font-weight: 500;
    }

    .navLink {
        position: relative;
        color: black;
        font-size: 15px;
        line-height: 1.15;
        font-family: 'Montserrat';
    }